.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 24px;
    background-size: cover;
    min-height: 100vh;
}

.pop-over {
    position: relative;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
}

.pop-over__body {
    display: flex;

    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.pop-over__top {
    position: absolute;
    top: -191px;
    height: 191px;
    width: 100%;
    background-size: cover;
    object-fit: cover;   
    opacity: .8; 
}

.pop-over__title {
    font-family: 'jaf-johannes-original';
    color: #fff;
}

.pop-over__body > p  {
    color: #fff;
    font-family: "lores-9-narrow";
    padding: 0px 32px 16px 32px;
}

.pop-over__body > p > iframe {
    display: block;
    border: none;
    margin: 0 auto;
    padding: 24px 0;
    height: 200px;
}

.pop-over__body > p > a {
    color: #fff;
    width: 100%;
    display: block;
    text-align: center;
}