.controls-table-key {
    display: inline-block;
    /* outline: 1px solid red; */
    text-align: right;
    width: 25%;
    box-sizing: content-box;
}

.controls-table-value {
    display: inline-block;
    text-align: left;
    width: 40%;
    /* outline: 1px solid blue; */
    padding-left: 32px;
    box-sizing: content-box;
}

.button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    /* max-width: 400px; */
}

.faux-button {
    border: 2px solid white;
    padding: 10px 16px 8px 16px;
    margin: 0px 12px;
    cursor: pointer;
}


.speech-bubble {
    z-index: 300;
    background-color: white;
    border: 3px solid black;
    border-radius: 14px;
    position: absolute;
    /* right: 100px; */
    /* bottom: 100px; */
    width: 400px;
    font-family: 'manaspc';
    padding: 24px;
    font-size: 24px;
}

.select-nft {
  position: fixed;
  top: 0px;
  right: 0px;
  width: 300px;
  text-align: right;
}

