body {
  margin: 0px;
}

.build-info {
  z-index: 4;
  position: fixed;
  bottom: 2px;
  right: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 2px 4px;
  padding-top: 4px;
  font-family: "manaspc";
  color: white;
  line-height: 16px;
}

.loc {
  z-index: 4;
  position: fixed;
  bottom: 2px;
  left: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 2px 4px;
  padding-top: 4px;
  font-family: "manaspc";
  color: white;
  line-height: 16px;
}

.press-e {
  width: 190px;
  left: 0px;
  right: 0px;
  bottom: 80px;
  height: 20px;
  border-radius: 5px;
  margin: 0px auto;
  text-align: center;
  background-color: #aaa;
  color: #333;
  font-weight: 700;
  padding-top: 6px;
}

.show-controls {
  left: auto;
  right: 4px;
  bottom: 26px;
  display: none;
}

.stats {
  position: fixed;
  left: 0px;
  top: 0px;
  opacity: 1;
  padding: 4px;
}

.stats > canvas {
  background: rgba(255, 255, 255, 0.33);
  outline: 1px solid #111;
  display: block;
  margin-bottom: -1px;
}

.disclaimer {
  text-align: center;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5vh 48px;
  font-family: "manaspc";
  color: white;
  font-size: 16px;
  line-height: 24px;
  /* display: none; */
}

@keyframes loading_flash {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.loading {
  text-align: center;
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 10vh;
  z-index: 8;
  padding: 5vh 48px;
  font-family: "manaspc";
  color: white;
  font-size: 64px;
  line-height: 24px;
  animation-name: loading_flash;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
}

.faq {
  text-align: center;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.75);
  padding: 5vh 48px;
  font-family: "manaspc";
  color: white;
  font-size: 16px;
  line-height: 24px;
  /* display: none; */
}

.controls {
  text-align: center;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5vh 48px;
  font-family: "manaspc";
  color: white;
  font-size: 16px;
  line-height: 16px;
}

.controls-table-row {
  padding: 4px;
  width: 100%;
  display: inline-block;
  box-sizing: content-box;
  margin-bottom: 12px;
}

.controls-table-key {
  display: inline-block;
  /* outline: 1px solid red; */
  text-align: right;
  width: 25%;
  box-sizing: content-box;
}

.controls-table-value {
  display: inline-block;
  text-align: left;
  width: 40%;
  /* outline: 1px solid blue; */
  padding-left: 32px;
  box-sizing: content-box;
}

.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* max-width: 400px; */
}

.faux-button {
  border: 2px solid white;
  padding: 10px 16px 8px 16px;
  margin: 0px 12px;
  cursor: pointer;
}
