.logo {
  width: 1.07em;
  margin-right: 0.3em;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #46403b;
}

.spacer {
  width: 1.5rem;
}

.connectButton {
  width: 380px;
  height: 100px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  cursor: pointer;
  text-align: center;
  font-size: xx-large;
  font-family: vikingFilled;
  background-color: #46403b;
  box-shadow: 2px 2px #000;
}

.connect-button > svg {
  margin-right: 10px;
}
